import React, { useEffect, useState } from 'react'
import InfoDialog from '../components/Dialog/InfoDialog'
import { fetchAPI } from '../services/Api';
import EditIcon from '@material-ui/icons/Edit';
import Slots from '../components/Form/SlotsForm'
import { connect } from 'react-redux'
import { default as ErrorImg } from '../assets/icons/warning.svg';
import { ADMIN } from '../services/Constants'
import AdminMenu from '../components/Admin/AdminMenu'
import BookingSection from '../components/Admin/BookingSection'
import GallerySection from '../components/Admin/GallerySection'
import VideoSection from '../components/Admin/VideoSection'
import PackageSection from '../components/Admin/PackageSection'
import ContactSection from '../components/Admin/ContactSection'
import TestimonialSection from '../components/Admin/TestimonialSection'

function AdminPage(props: any) {
    const [operatingHours, setOperatingHours] = useState([]);
    const [visibleSection, setVisibleSection] = useState('appointment')

    useEffect(() => {
        if (props.userDetails.userType !== ADMIN) {
            return
        }
    }, [props.userDetails.userType])

    useEffect(() => {
        getOperatingHours();
    }, []);

    const unAuthorisedUser = <div className="m-5 d-flex justify-cintent-center align-items-center flex-column">
        <img alt="erro" src={ErrorImg} className="m-5" width="100px" />
        You are not authorised to view this page. Please Login as Administrator to continue.
    </div>


    const getOperatingHours = () => {
        fetchAPI("operatingSlots").then((res: any) => {
            setOperatingHours(res || [])
        })
    }


    const createEditComponent = (params: any) => {
        return <Slots {...params} cb={getOperatingHours} />
    }

    const operatingHourComponent = () => {
        return <ul className="list-unstyled text-left">
            {operatingHours.map((day: any) =>
                <li key={day.key} className="p-1 border-bottom d-flex justify-content-between align-items-center">{day.label}
                    <span className={`${day.isClosed ? 'text-danger' : ''}`} > {day.isClosed ? 'Closed' : `${day.startTime} - ${day.endTime}`}
                        <InfoDialog
                            isIconButton={true}
                            iconButton={<EditIcon color="primary" />}
                            btnLabel="Edit"
                            btnVariant="contained"
                            title={`Edit ${day.label || ''}`}
                            component={createEditComponent(day)}
                        />
                    </span>
                </li>)}
        </ul>
    }

    const showSection = (section: string) => {
        setVisibleSection(section)
    }

    if (props.userDetails.userType !== ADMIN) {
        return unAuthorisedUser
    }

    return <>
        <>
            <div className="admin_container">
                <AdminMenu visibleSection={visibleSection} showSection={showSection} />
                <div className="admin_content">
                    {visibleSection === 'appointment' && <BookingSection {...props} />}
                    {visibleSection === 'image' && <GallerySection {...props} />}
                    {visibleSection === 'video' && <VideoSection {...props} />}
                    {visibleSection === 'services' && <PackageSection {...props} />}
                    {visibleSection === 'timing' && operatingHourComponent()}
                    {visibleSection === 'testimonials' && <TestimonialSection {...props} />}
                    {visibleSection === 'contact' && <ContactSection {...props} />}
                </div>
            </div>
        </>
    </>
}

const mapStateToProps = (state: any) => {
    return {
        slotDetails: state.SlotReducer.slotDetails,
        userDetails: state.UserReducer.userDetails,
        contactDetails: state.UserReducer.contactDetails
    }
}

export default connect(mapStateToProps, {})(AdminPage)