import { Paper } from '@material-ui/core';
import { MDBCol, MDBRow } from 'mdbreact';
import React, { useEffect, useState } from 'react'
import { updateNotofication } from '../../Actions/ActionTypes';
import { CSS } from '../../assets/style';
import { call } from '../../services/Api';
import { ADD_TESTIMONIALS_FORM } from '../../services/Form';
import ActionButton from '../ActionButton';
import InfoDialog from '../Dialog/InfoDialog';
import TestimonialForm from '../Form/TestimonialForm';
import Loading from '../RainyDay/Loading'
import NotFound from '../RainyDay/NotFound'

export default function TestimonialSection(props: any) {
    const [testimonialList, setTestimonialList] = useState([]);
    const [isApiCallInProgress, setIsApiCallInProgress] = useState(false);

    useEffect(() => {
        getTestimonialList();
    }, [])

    const getTestimonialList = () => {
        setIsApiCallInProgress(true)
        call("testimonials", "GET").then((response: any) => {
            setIsApiCallInProgress(false)
            if (response.error || response.message) {
                updateNotofication({ type: "error", message: response.error || response.message });
            } else {
                let updatedResposne = response.map((testimonial: any) => {
                    return {
                        ...testimonial, ...{
                            content: testimonial.content.split('##')[2],
                            designation: testimonial.content.split('##')[1],
                            rating: testimonial.content.split('##')[0]
                        }
                    }
                })
                setTestimonialList(updatedResposne || [])
            }
        })
    }

    if (isApiCallInProgress) {
        return <Loading name="Testimonials" />
    }

    if (testimonialList.length === 0) {
        return <NotFound listType="Testimonials" />
    }

    return <>
        <h1>Testimonials</h1>
        <InfoDialog
            btnLabel="Add Testimonial"
            title={`Add New Testimonial`}
            component={<TestimonialForm cb={getTestimonialList} {...ADD_TESTIMONIALS_FORM} />}
        />
        <MDBRow className="m-0 mt-3">
            {testimonialList.map((listItem: any) => <MDBCol lg="6" className="text-center mt-3" key={listItem.id}>
                <Paper elevation={3} className="p-3">
                    {/* <Avatar className='mx-auto mb-4'>
                    <img
                        src={listItem.imageSrc}
                        className='rounded-circle img-fluid'
                        alt=''
                    />
                </Avatar> */}
                    <p style={{ ...CSS.smallHeader }}>{listItem.author}</p>
                    <h6 className='my-3' style={{ ...CSS.header }}>{listItem.designation}</h6>
                    <p >{listItem.content}</p>
                    <p className="text-danger">{listItem.rating} Star</p>
                    <ActionButton editComponent={<TestimonialForm {...props} data={listItem} cb={getTestimonialList} />} {...listItem} {...ADD_TESTIMONIALS_FORM} cb={getTestimonialList} />
                </Paper>
            </MDBCol>)}
        </MDBRow>
    </>
}