import { TextField } from '@material-ui/core';
import React, { useEffect, useState } from 'react'
import { updateNotofication } from '../../Actions/ActionTypes';
import { call } from '../../services/Api';
import { ADD_TESTIMONIALS_FORM, getValue, validateFormAndMessage } from '../../services/Form'
import CustomBtn from '../CustomBtn';
import Error from '../RainyDay/Error';

export default function TestimonialForm(props: any) {
    const [formData, setFormData] = useState({} as any);
    const [errorMessage, serErrorMessage] = useState('');
    const [isApiCallInProgress, setIsApiCallInProgress] = useState(false);
    let isEdit: boolean = props.data ? true : false;

    useEffect(() => {
        setFormData({ ...formData, ...props.data });
    }, [])

    const handleChange = (stateName: string, value: string) => {
        setFormData({ ...formData, [stateName]: value });
    }

    const updateTestimonials = () => {
        serErrorMessage('');
        if (validateFormAndMessage(formData, ADD_TESTIMONIALS_FORM).isInvalid) {
            serErrorMessage(validateFormAndMessage(formData, ADD_TESTIMONIALS_FORM).errorMessage)
            return
        }

        setIsApiCallInProgress(true);
        let requestParam: any = { ...formData };
        requestParam.content = `${requestParam.rating}##${requestParam.designation}##${requestParam.content}`;

        call("testimonials", isEdit ? 'PUT' : 'POST', JSON.stringify(requestParam)).then((response: any) => {
            setIsApiCallInProgress(false)
            if (response.error || response.message) {
                updateNotofication({ type: "error", message: response.error || response.message });
            } else {
                updateNotofication({ type: "success", message: isEdit ? "Updated Suceesfully" : "Added Successful" });
                props.cb()
            }
        })
    }

    return <>
        {ADD_TESTIMONIALS_FORM.map((inputField: any) => {
            return <TextField
                key={inputField.key}
                className="w-100"
                variant="outlined"
                size={"small"}
                value={getValue(inputField.key, formData)}
                onChange={(e) => { handleChange(inputField.key, e.target.value) }}
                margin="normal"
                label={inputField.label}
                type={inputField.type}
            />
        })}
        <div className="d-flex justify-content-center w-100 mt-3">
            <CustomBtn
                disabled={isApiCallInProgress}
                handleButtonClick={updateTestimonials}
                btnLabel={isApiCallInProgress ? "Saving..." : isEdit ? "Update" : "Add"} />
        </div>
        <Error errorMessage={errorMessage} />
    </>
}