import React, { useEffect, useState } from 'react'

export default function CustomIcon(props: any) {
    const [image, setImage] = useState({} as any);

    useEffect(() => {
        loadImage()
    }, [])

    const loadImage = () => {
        import(`../assets/icons/svg/${props.icon}.svg`).then(image => {
            setImage(image)
        });
    };

    const changeImage = () => {
        import(`../assets/icons/svg_gradiant/${props.icon}.svg`).then(image => {
            setImage(image)
        });
    }
    return <img
        height={props.size || "40px"}
        src={image.default}
        alt="icon"
        className="mr-2 pointer"
        onMouseEnter={changeImage}
        onMouseLeave={loadImage}
    />
}