import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import StepContent from '@material-ui/core/StepContent';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Calendar from '../Calendar/Calendar';
import CustomBtn from '../CustomBtn';
import Time from '../Calendar/Time';
import { connect } from 'react-redux';
import { updateNotofication, updateSlots, updateCart } from '../../Actions/ActionTypes';
import { LoginNew } from '../Login/LoginNew';
import Login from '../Login/Login';
import { call, fetchAPI } from '../../services/Api';
import { MDBCol, MDBRow } from 'mdbreact';
import { COLORS } from '../../assets/style';
import { Link } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
    button: {
        marginTop: theme.spacing(1),
        marginRight: theme.spacing(1),
    },
    actionsContainer: {
        marginBottom: theme.spacing(2),
    },
    resetContainer: {
        padding: theme.spacing(3),
    },
}));

function getChip(label: string) {
    return label || ''
}

function getSteps(slotDetails: any, userDetails: any) {
    return [`Select Date ${getChip(slotDetails?.date)}`,
    `Slect Time ${getChip(slotDetails?.time)}`,
    `${userDetails._id ? 'Complete Booking' : 'Register or Login'}`];
}

const covertToAppointmentDateTime = (slots: any) => {
    return `${slots.date.split('/')[2]}-${slots.date.split('/')[0]}-${slots.date.split('/')[1]} ${slots.time}`
}

const bookAppointment = (props: any) => {
    let requestParam = {
        "serviceIds": props.cartItems.map((service: any) => service._id),
        "userId": props.userDetails._id,
        "appointmentTime": covertToAppointmentDateTime(props.slotDetails)
    }

    call('appointment', 'POST', JSON.stringify(requestParam)).then((response: any) => {
        if (response.errors || response.error) {
            updateNotofication({ type: "error", message: response._message || response.error });
        } else {
            updateNotofication({ type: "success", message: "Appointmet Scheduled" });
            // setOpen(false);
            props.updateCart([]);
            props.hanldeClose();
        }
    })
}

const callPaytm = () => {
    let paymentDetails = {
        amount: 100,
        customerId: 'Anup',
        customerEmail: 'def',
        customerPhone: '9999999999'
    }
    fetchAPI("paynow", "POST", paymentDetails)
}

function getStepContent(step: number, props: any) {
    switch (step) {
        case 0:
            return <Calendar slotDetails={props.slotDetails} />
        case 1:
            return <Time slotDetails={props.slotDetails} />
        case 2:
            return props.userDetails._id ?
                <div className="text-center m-3">
                    <CustomBtn
                        fullWidth
                        btnLabel="Confirm Booking"
                        handleButtonClick={() => bookAppointment(props)} />
                </div> :
                <MDBRow className="m-0">
                    <MDBCol lg="6">
                        <Paper elevation={2} className="p-3 shadow h-100 top-border-green">
                            <h4>Booking Details</h4>
                            <hr />
                            <p>Date : <b style={{ color: COLORS.app_orange }}> {props.slotDetails.date}</b></p>
                            <p>Time : <b style={{ color: COLORS.app_orange }}> {props.slotDetails.time} </b></p>
                            <p>Services : <b style={{ color: COLORS.app_orange }}> {props.cartItems.map((service: any) => service.name).join()} </b></p>
                        </Paper>
                    </MDBCol>
                    <MDBCol lg="6">
                        <Button size="small" variant="outlined" onClick={callPaytm}>
                            Pay Now
                        </Button>
                        <Login booking successCB={() => bookAppointment(props)} modalCb={() => { }} />
                        <br />
                        <LoginNew booking button />
                    </MDBCol>
                </MDBRow>
        default:
            return 'Unknown step';
    }
}

function BookingStepper(props: any) {
    const classes = useStyles();
    const [activeStep, setActiveStep] = React.useState(0);
    const steps = getSteps(props.slotDetails, props.userDetails);

    const handleNext = () => {
        setActiveStep((prevActiveStep) => prevActiveStep + 1);
    };

    const handleBack = () => {
        setActiveStep((prevActiveStep) => prevActiveStep - 1);
    };

    const goToStep = (step: number) => {
        if (step > activeStep) {
            return
        }
        setActiveStep(step);
    };

    const getBackButtonText = (step: number) => {
        let btnText = 'Back';
        switch (step) {
            case 1: {
                btnText = "Change Date";
                break
            }
            case 2: {
                btnText = "Change Time";
                break
            }
            default:
                break;
        }
        return btnText
    }

    return (
        <div className="p-0 w-100">
            <Stepper activeStep={activeStep} orientation="vertical" className="p-0">
                {steps.map((label, index) => (
                    <Step key={label}>
                        <StepLabel onClick={() => { goToStep(index) }}>{label}</StepLabel>
                        <StepContent className="p-0">
                            {getStepContent(index, props)}
                            <div className={classes.actionsContainer}>
                                <div className="text-center mt-3">
                                    {activeStep ? <Button
                                        className="m-0 mr-3"
                                        size="small"
                                        color="secondary"
                                        variant="outlined"
                                        onClick={handleBack}
                                    >{getBackButtonText(activeStep)}</Button> : null}
                                    {activeStep === 2 ? null : <CustomBtn
                                        disabled={activeStep === 1 && !props.slotDetails.time}
                                        handleButtonClick={handleNext}
                                        btnLabel='Next'
                                    />}
                                </div>
                            </div>
                        </StepContent>
                    </Step>
                ))}
            </Stepper>
        </div>
    );
}

const mapStateToProps = (state: any) => {
    return {
        slotDetails: state.SlotReducer.slotDetails,
        userDetails: state.UserReducer.userDetails,
        cartItems: state.UserReducer.cartItems,
    }
}

export default connect(mapStateToProps, { updateSlots, updateCart })(BookingStepper)
