import React, { useEffect, useState } from "react";
import { MDBCol, MDBContainer, MDBRow } from "mdbreact";
import BasicCard from "../components/Card/BasicCard";
import FeatureCard from "../components/Card/FeatureCard";
import TestimonialsPage from "../components/Card/TestimonialCard";
import Heading from "../components/Heading";
import TopCarousel from "../components/TopCarousel";
import { call } from "../services/Api";
import HeaderInfoCard from "../components/Card/HeaderInfoCard";
import LandingPageCard from "../components/Card/LandingPageCard";
import ViewMoreCard from "../components/Card/ViewMoreCard";
import { updateNotofication } from "../Actions/ActionTypes";
import LandingPageRegisterCard from "../components/Card/LandingPageRegisterCard";
import NotFound from "../components/RainyDay/NotFound";

export default function LandingPage() {
    const [packageList, setPackageList] = useState([]);
    const [showViewMore, setShowViewMore] = useState(false);

    useEffect(() => {
        getPackageList();
    }, [])

    const getPackageList = () => {
        call('service', 'GET').then((res: any) => {
            if (res.error || res.message) {
                updateNotofication({ type: "error", message: res.error || res.message });
            } else {
                if (res.length > 3) {
                    res = res.splice(0, 2)
                    setShowViewMore(true)
                }
                setPackageList(res || [])
            }
        })
    }
    return (
        <>
            <TopCarousel />
            <HeaderInfoCard />

            <MDBContainer>
                <Heading heading1="why" heading2="Choose Us?" />
                <FeatureCard />
            </MDBContainer>

            <LandingPageCard />

            <HeaderInfoCard static />

            <MDBContainer>
                <Heading heading1="our" heading2="Services" />
                {packageList.length === 0 ? <NotFound listType="Services" /> : <MDBRow className=" m-0">
                    {packageList.map((packages: any) => <MDBCol key={packages._id} lg="4">
                        <BasicCard {...packages} cb={getPackageList} hideActionButton />
                    </MDBCol>)}
                    {showViewMore && <ViewMoreCard link="services" />}
                </MDBRow>}
            </MDBContainer>

            <LandingPageRegisterCard />

            <TestimonialsPage />

            <Heading heading1="Locate" heading2="Us" id="locateUs" />
            <iframe
                className=""
                title="map"
                width="100%"
                height="600"
                id="gmap_canvas"
                src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3782.3138333050524!2d73.77938061530857!3d18.559884772813724!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3bc2becfb828e839%3A0x5375ed2872864fd3!2s1%2C%20Pan%20Card%20Club%20Rd%2C%20Baner%2C%20Pune%2C%20Maharashtra%20411045!5e0!3m2!1sen!2sin!4v1616481108494!5m2!1sen!2sin"
                frameBorder="0"
                scrolling="no">
            </iframe>

        </>
    );
}