import { MDBMask, MDBView, MDBRow, MDBCol } from "mdbreact";
import React, { useEffect, useState } from 'react';
import Heading from '../components/Heading';
import NotFound from "../components/RainyDay/NotFound";
import { call } from "../services/Api";
import ImageGallery from 'react-image-gallery';
import "../../node_modules/react-image-gallery/styles/css/image-gallery.css";
import { Dialog, DialogContent, DialogTitle, IconButton, makeStyles, Theme, createStyles, Hidden, Button } from "@material-ui/core";
import CloseIcon from '@material-ui/icons/Close';
import CustomBtn from "../components/CustomBtn";
import { updateNotofication } from "../Actions/ActionTypes";
import MediaCard from "../components/Media/MediaCard";
import Loading from "../components/RainyDay/Loading";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        closeButton: {
            position: 'absolute',
            right: theme.spacing(5),
            top: theme.spacing(5),
            zIndex: 999
        },
    })
);

export default function Gallery() {
    const classes = useStyles();
    const [isApiCallInProgress, setIsApiCallInProgress] = useState(false);
    const [mediaList, setMediaList] = useState([]);
    const [open, setOpen] = useState(false);
    const [slideIndex, setSlideIndex] = useState(0)

    useEffect(() => {
        getmediaList();
    }, []);

    const getmediaList = () => {
        setIsApiCallInProgress(true)
        call("upload/media", "GET").then((res: any) => {
            setIsApiCallInProgress(false)
            if (res.error || res.message) {
                updateNotofication({ type: "error", message: res.error || res.message });
            } else {
                res = res && res.map((element: any) => {
                    return {
                        ...element,
                        ...{ original: element.src, thumbnail: element.src }
                    }
                })
                setMediaList(res || [])
            }
        })
    }

    if (isApiCallInProgress) {
        return <Loading name="Images" />
    }

    return <div className="bg-dark">
        <Heading heading1="our Gallery" heading2="" />
        {mediaList.length === 0 ? <NotFound listType="Media" /> : <MDBRow>
            {mediaList.map((media: any, index: number) => <MDBCol md="3" onClick={() => { setOpen(true); setSlideIndex(index) }}>
                <MDBView hover className="btn p-0">
                    <MediaCard {...media} />
                    <MDBMask className="flex-center" overlay="black-strong">
                        <p className="white-text">{media.title}</p>
                    </MDBMask>
                </MDBView>
            </MDBCol>)}
        </MDBRow>}
        <Dialog
            onBackdropClick={() => { setOpen(false) }}
            PaperProps={{
                style: {
                    backgroundColor: 'black',
                    boxShadow: 'none',
                },
            }}
            fullScreen
            open={open}
            onEscapeKeyDown={() => { setOpen(false) }}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
        >
            <Hidden mdDown>
                <DialogTitle id="alert-dialog-title">
                    <IconButton aria-label="close" className={classes.closeButton} onClick={() => { setOpen(false) }}>
                        <CloseIcon fontSize="large" color="secondary" />
                    </IconButton>
                </DialogTitle>
            </Hidden>
            <DialogContent className="text-center" style={{ height: 'calc(80vh)' }}>
                <ImageGallery startIndex={slideIndex} items={mediaList} />
                <Hidden lgUp>
                    <CustomBtn btnLabel="Done" handleButtonClick={() => { setOpen(false) }} />
                </Hidden>
            </DialogContent>
        </Dialog>
    </div>
}