import { Button, TextField } from '@material-ui/core';
import React, { useState } from 'react'
import {  CSS } from '../../assets/style';
import Error from '../RainyDay/Error';
import { call, callFormApi } from '../../services/Api';
import { updateNotofication } from '../../Actions/ActionTypes';
import { getValue } from '../../services/Form';
import { MDBCol, MDBRow } from 'mdbreact';
import CustomBtn from '../CustomBtn';

export default function UploadFile(props: any) {
    const [formData, setFormData] = useState({}) as any;
    const [isApiCallInProgress, setIsApiCallInProgress] = useState(false);
    const [file, setFile] = useState([]);
    const [errorMessage, serErrorMessage] = useState('');

    const resetAll = () => {
        setFile([]);
        setIsApiCallInProgress(false);
        serErrorMessage('');
    }

    const handleChange = (stateName: string, value: string) => {
        setFormData({ ...formData, [stateName]: value });
    }

    const deleteFile = (index: number) => {
        let allFiles: any = [...file];
        allFiles.splice(index, 1);
        setFile(allFiles);
    }

    const onFileChange = (files: any) => {
        serErrorMessage('');
        let allFiles: any = [...file];
        allFiles = [...allFiles, ...files];
        setFile(allFiles);
        let allFormData: any = {};
        allFiles.forEach((element: any, index: number) => {
            allFormData = {
                ...allFormData,
                [`name${index}`]: element.name,
                [`description${index}`]: `${element.name} Description`
            }
        });
        setFormData({
            ...formData,
            ...allFormData
        });
    };

    const uploadMedia = () => {
        if (file.length === 0) {
            serErrorMessage('No Media to upload');
            return
        }

        setIsApiCallInProgress(true);
        let formDataRequest = new FormData();
        file.forEach((item: any) => {
            formDataRequest.append(
                "files",
                item
            );
        });

        callFormApi(`upload`, 'POST', formDataRequest).then((response: any) => {
            if (response.error) {
                serErrorMessage(response.error);
                updateNotofication({ type: "error", message: "Media Upload Failed" });
            } else {
                let mediaList: any = [];
                file.forEach((item: any, index: any) => {
                    mediaList.push({
                        id: index,
                        title: formData[`name${index}`],
                        description: formData[`description${index}`],
                        src: response.locationArray[index]
                    })
                })
                let mediaRequest = {
                    media: mediaList
                }
                call('upload/media', 'POST', JSON.stringify(mediaRequest)).then((res: any) => {
                    if (res.error) {
                        serErrorMessage(res.error);
                        updateNotofication({ type: "error", message: "Media Upload Failed in Step2" });
                    } else {
                        updateNotofication({ type: "success", message: "Media Uploaded" });
                        resetAll()
                        props.cb()
                    }
                })
            }
        })
    }

    return <>
        <span className="customBtn">
            <Button
                style={{ ...CSS.btn }}
                size="small"
                variant="outlined"
                component="label"
            >
                {file.length === 0 ? `Add ${props.video ? 'Videos' : 'Images'}` : "Add More"}
                <input
                    multiple
                    accept={props.video ? "video/*" : "image/*"}
                    type="file"
                    style={{ display: "none" }}
                    onChange={(e) => onFileChange(e.target.files)}
                />
            </Button>
        </span>
        {file.length > 0 && <div className="text-center m-3">
            <CustomBtn disabled={isApiCallInProgress} btnLabel={isApiCallInProgress ? "Uploading..." : "Upload"} handleButtonClick={uploadMedia} />
            <Error errorMessage={errorMessage} />
        </div>}
        <MDBRow className="m-0">
            {file.length === 0 ? '' : file.map((item: any, index: number) => <MDBCol lg="3" className="shadow mb-3" key={index}>
                {props.video ? <iframe style={{ "minHeight": "300px" }} className="w-100 border-0" title="video" src={URL.createObjectURL(item)} allowFullScreen></iframe>
                    : <img className="w-100" alt="image_icon" src={URL.createObjectURL(item)} />}
                <br />
                <TextField
                    className="w-100"
                    variant="outlined"
                    size={"small"}
                    value={getValue(`name${index}`, formData)}
                    onChange={(e) => { handleChange(`name${index}`, e.target.value) }}
                    margin="normal"
                    label="Image Name"
                />
                <TextField
                    className="w-100"
                    variant="outlined"
                    size={"small"}
                    value={getValue(`description${index}`, formData)}
                    onChange={(e) => { handleChange(`description${index}`, e.target.value) }}
                    margin="normal"
                    label="Image Description"
                />
                <div className="text-center m-2">
                    <Button
                        color="secondary"
                        onClick={() => { deleteFile(index) }}
                        size="small"
                        variant="outlined">Remove</Button>
                </div>
            </MDBCol>)}
        </MDBRow>
    </>
}