import { MDBCol, MDBRow } from 'mdbreact'
import React, { useEffect, useState } from 'react'
import { updateNotofication } from '../../Actions/ActionTypes'
import { call } from '../../services/Api'
import { ADD_IMAGE_FORM } from '../../services/Form'
import { checkIfVideo } from '../../services/util'
import BasicCard from '../Card/BasicCard'
import UploadFile from '../Dialog/UploadFile'
import Loading from '../RainyDay/Loading'
import NotFound from '../RainyDay/NotFound'

export default function GallerySection(props: any) {
    const [mediaList, setMediaList] = useState([]);
    const [isApiCallInProgress, setIsApiCallInProgress] = useState(false);

    useEffect(() => {
        getMediaList();
    }, [])

    const getMediaList = () => {
        setIsApiCallInProgress(true)
        call("upload/media", "GET").then((response: any) => {
            setIsApiCallInProgress(false)
            if (response.error || response.message) {
                updateNotofication({ type: "error", message: response.error || response.message });
            } else {
                setMediaList(response || [])
            }
        })
    }
    
    if (isApiCallInProgress) {
        return <Loading name="Gallery" />
    }

    if (mediaList.length === 0) {
        return <NotFound listType="Gallery" />
    }

    return <>
        <UploadFile cb={getMediaList} />
        <h1>Gallery</h1>
        <MDBRow>
            {mediaList.filter((element: any) => !checkIfVideo(element)).map((image: any) => <MDBCol key={image._id} lg="4">
                <BasicCard {...image} {...ADD_IMAGE_FORM} cb={getMediaList} isAdmin={props.userDetails.userType} />
            </MDBCol>)}
        </MDBRow>
    </>
}