import { Paper, TextField } from '@material-ui/core'
import React, { useEffect, useState } from 'react'
import { CONTACT_FORM, getValue } from '../../services/Form'
import CustomBtn from '../CustomBtn';
import InfoDialog from '../Dialog/InfoDialog'
import Error from '../RainyDay/Error';
import { get } from 'lodash'
import { EMAIL_REGEX, PHONE_REGEX } from '../../services/Constants';
import { call } from '../../services/Api';
import { CONTACT_DETAILS, updateNotofication } from '../../Actions/ActionTypes';
import store from '../../Reducers';
import { MDBCol, MDBRow } from 'mdbreact';
import Loading from '../RainyDay/Loading';
import NotFound from '../RainyDay/NotFound';

export default function ContactSection(props: any) {
    const [formData, setFormData] = useState({} as any);
    const [errorMessage, serErrorMessage] = useState('');
    const [contactDetails, setContactDetails] = useState({});
    const [isApiCallInProgress, setIsApiCallInProgress] = useState(false);

    useEffect(() => {
        getContactDetails();
        setFormData({ ...formData, ...props.contactDetails });
    }, [props.contactDetails]);

    const getContactDetails = () => {
        setContactDetails(props.contactDetails)
    }

    const updateContactDetails = () => {
        call("contact", "GET").then((res: any) => {
            store.dispatch({
                type: CONTACT_DETAILS,
                contactDetails: res[0]
            })
        })
    }

    const handleChange = (stateName: string, value: string) => {
        setFormData({ ...formData, [stateName]: value });
    }

    const updateContact = () => {
        let formDataRequest: any = { ...formData };

        let isInvaildPhoneNumber: boolean = false;
        let isInvalidEmail: boolean = false;
        let isInvalidAddress: boolean = false;
        if (typeof (formDataRequest.phoneNumbers) === 'string') {
            formDataRequest.phoneNumbers.split(',').forEach((phone: string) => {
                if (phone.length !== 10 || !PHONE_REGEX.test(phone)) {
                    serErrorMessage('One or more Phone Number is Incorrect');
                    isInvaildPhoneNumber = true
                }
            })
        }
        if (formData.emailId.length === 0 || !EMAIL_REGEX.test(formData.emailId)) {
            serErrorMessage('Invalid Email Id');
            isInvalidEmail = true
        }

        if (formData.address.length === 0) {
            serErrorMessage('Address should not be empty');
            isInvalidAddress = true
        }

        if (isInvaildPhoneNumber || isInvalidEmail || isInvalidAddress) {
            return
        } else {
            formDataRequest.phoneNumbers = typeof (formDataRequest.phoneNumbers) === 'string' ?
                formDataRequest.phoneNumbers.split(',') :
                formDataRequest.phoneNumbers
        }
        serErrorMessage('');
        setIsApiCallInProgress(true);

        call('contact', 'PUT', JSON.stringify(formDataRequest)).then((response: any) => {
            setIsApiCallInProgress(false)
            if (response.error || response.message) {
                serErrorMessage(response.error || response.message);
                updateNotofication({ type: "error", message: response.error || response.message });
            } else {
                updateNotofication({ type: "success", message: "Updated Suceesfully" });
                updateContactDetails()
            }
        })
    }

    const createContactForm = () => {
        return <>
            {CONTACT_FORM.map((inputField: any) => {
                return <TextField
                    key={inputField.key}
                    className="w-100"
                    variant="outlined"
                    size={"small"}
                    value={getValue(inputField.key, formData)}
                    onChange={(e) => { handleChange(inputField.key, e.target.value) }}
                    margin="normal"
                    label={inputField.label}
                    type={inputField.type}
                />
            })}
            <div className="d-flex justify-content-center w-100 mt-3">
                <CustomBtn disabled={isApiCallInProgress} handleButtonClick={updateContact} btnLabel={isApiCallInProgress ? "Updating..." : "Update"} />
            </div>
            <Error errorMessage={errorMessage} />
        </>
    }

    return <>
        <InfoDialog
            btnLabel="Edit"
            title={`Update Contact Details`}
            component={createContactForm()}
        />
        <MDBRow>
            <MDBCol lg="6" className="shadow rounded p-3 bg-info mt-3">
                <ul className="list-unstyled text-left">
                    <li className="py-3 text-white border-bottom d-flex justify-content-between align-items-center"><b>Phone</b>
                        <div className="text-right">
                            {get(contactDetails, 'phoneNumbers', []).map((phone: string) => <p className="text-white m-0">{phone}</p>)}
                        </div>
                    </li>
                    <li className="py-3 text-white border-bottom d-flex justify-content-between align-items-center"><b>Email</b>
                        <div className="text-right">
                            {get(contactDetails, 'emailId')}
                        </div>
                    </li>
                    <li className="py-3 text-white border-bottom d-flex justify-content-between align-items-center"><b>Address</b>
                        <div className="text-right">
                            {get(contactDetails, 'address', null)}
                        </div>
                    </li>
                    {/* <li className="p-1 border-bottom d-flex justify-content-between align-items-center">Social Network
     {contactDetails.social ? <div className="text-right">
                    <p>Facebook : {get(contactDetails.social, 'fb', null)}</p>
                    <p>Instagram : {get(contactDetails.social, 'insta', null)}</p>
                    <p>Twitter : {get(contactDetails.social, 'twitter', null)}</p>
                </div> : null}
            </li> */}
                </ul>
            </MDBCol>
        </MDBRow>
        <p className="text-danger mt-3">Note: The above details will be shown in the footer and the Landing Page.</p>
    </>
}