import React from 'react';
import { MDBView, MDBCarousel, MDBCarouselInner, MDBCarouselItem, MDBMask } from 'mdbreact';
import CustomBtn from './CustomBtn';
import Booking from '../pages/Booking';
import { Link } from 'react-router-dom';
import PageBanner from '../pages/PageBanner';


export default function TopCarousel() {
    const carouselList = [{
        title: 'Do you want to feel better?',
        description: 'Get help now with a nutritional therapy session to boost energy, improve your mood and tackle any ill health issues.',
        url: 'https://bcphysio.org/sites/default/files/styles/banner_large/public/images/2019-09/Banner%20-%20For%20The%20Public.jpg?itok=KJ-k7G91',
        component: <Booking />,
        otherDetails: [
            "Knees Problem",
            "Back Pain",
            "Shoulder Injuries",
            "Cervical Problems",
        ]
    },
    {
        title: 'Easy book & pay online',
        description: 'Choose your time, pay & receive confirmation all in the comfort of your own home.',
        url: 'https://www.educocare.com/wp-content/uploads/2020/10/book-appointment.jpg',
        component: <Booking />,
        otherDetails: [
            "Pick a Service",
            "Select Date",
            "Check your slot",
            "Easy Payment"
        ]
    },
    {
        title: 'Do you want weight loss results that last?',
        description: 'Learn how to lose weight and keep it off!',
        url: 'https://bhrcenter.com/wp-content/uploads/2016/03/Weight-Loss-Banner.jpg',
        component: <Link to="services"><CustomBtn btnLabel="Know More" /></Link>,
        otherDetails: [
            "Thyroid",
            "Weight Gain",
            "Fat Loss",
            "Muscle Gain"]
    }]
    return (
        <MDBCarousel
            interval={10000}
            activeItem={1}
            length={3}
            showControls={true}
            showIndicators={true}
            className="z-depth-1 top-carousel"
        >
            <MDBCarouselInner>
                {carouselList.map((listItem, index) => <MDBCarouselItem key={index} itemId={++index}>
                    <MDBView>
                        <PageBanner {...listItem} />
                        <MDBMask overlay="black-strong" />
                    </MDBView>
                </MDBCarouselItem>
                )}
            </MDBCarouselInner>
        </MDBCarousel>
    );
}