import React, { useEffect, useState } from 'react';
import {
    MDBCarousel,
    MDBCarouselInner,
    MDBCarouselItem,
    MDBIcon,
    MDBView,
} from 'mdbreact';
import { Avatar } from '@material-ui/core';
import { CSS } from '../../assets/style';
import { updateNotofication } from '../../Actions/ActionTypes';
import { call } from '../../services/Api';
import { convertToDateString } from '../../services/util';
import Heading from '../Heading';

const TestimonialsPage = () => {
    const [testimonialList, setTestimonialList] = useState([]);

    useEffect(() => {
        getTestimonialList();
    }, [])

    const getTestimonialList = () => {
        call('testimonials', 'GET').then((res: any) => {
            if (res.error || res.message) {
                updateNotofication({ type: "error", message: res.error || res.message });
            } else {
                setTestimonialList(res || [])
            }
        })
    }

    function getRatings(rating: number) {
        let starRating = [];
        for (let step = 0; step < rating; step++) {
            starRating.push(<MDBIcon key={step} icon='star' className='yellow-text' />)
        }
        if (rating < 5) {
            for (let step = 0; step < 5 - rating; step++) {
                starRating.push(<MDBIcon key={step + 'far'} far icon='star' className='yellow-text' />)
            }
        }
        return starRating
    }

    if (testimonialList.length === 0) {
        return null
    }

    return <>
        <Heading heading1="Client" heading2="Testimonials" />
        <section className='text-center my-5 py-5 parallax parallax_testimonials'>
            <MDBCarousel
                interval={10000}
                activeItem={1}
                length={testimonialList.length}
                className="no-flex testimonial-carousel"
            >
                <MDBCarouselInner>
                    {testimonialList.map((listItem: any, index) => <MDBCarouselItem key={listItem._id} itemId={++index}>
                        <MDBView className="w-75 m-auto">
                            <Avatar className='mx-auto mb-4'>
                                <img
                                    src={listItem.imageSrc}
                                    className='rounded-circle img-fluid'
                                    alt=''
                                />
                            </Avatar>
                            <h4 style={{ ...CSS.smallHeader }}>{listItem.author}</h4>
                            <h6 className='my-3' style={{ ...CSS.header }}>{listItem.content.split('##')[1]}</h6>
                            <p>
                                <MDBIcon className="mr-3" icon='quote-left' style={{ ...CSS.smallHeader }} />{listItem.content.split('##')[2]}
                            </p>
                            {getRatings(listItem.content.split('##')[0])}
                            <h6 className='my-3' style={{ ...CSS.header }}>{convertToDateString(listItem.createdDate)}</h6>
                        </MDBView>
                    </MDBCarouselItem>)}
                </MDBCarouselInner>
            </MDBCarousel>
        </section>
    </>
};

export default TestimonialsPage;