import { MDBCol, MDBRow } from 'mdbreact';
import React, { useEffect, useState } from 'react'
import { updateNotofication } from '../../Actions/ActionTypes';
import { call } from '../../services/Api';
import { ADD_PACKAGE_FORM } from '../../services/Form';
import BasicCard from '../Card/BasicCard';
import InfoDialog from '../Dialog/InfoDialog'
import AddNew from '../Form/AddNewForm';
import Loading from '../RainyDay/Loading'
import NotFound from '../RainyDay/NotFound'

export default function PackageSection(props: any) {
    const [cancelDialogOpen, setCancelDialogOpen] = useState(false);
    const [packageList, setPackageList] = useState([]);
    const [isApiCallInProgress, setIsApiCallInProgress] = useState(false);

    useEffect(() => {
        getPackageList();
    }, [])

    const getPackageList = () => {
        setIsApiCallInProgress(true)
        call('service', 'GET').then((response: any) => {
            setIsApiCallInProgress(false)
            if (response.error || response.message) {
                updateNotofication({ type: "error", message: response.error || response.message });
            } else {
                setPackageList(response || [])
            }
        })
        setCancelDialogOpen(false);
    }

    if (isApiCallInProgress) {
        return <Loading name="Services" />
    }

    if (packageList.length === 0) {
        return <NotFound listType="Services" />
    }

    return <>
        <h1>Sercices</h1>
        <InfoDialog
            open={cancelDialogOpen}
            setDialogOpen={setCancelDialogOpen}
            btnLabel="Add Service"
            title={`Add New Service`}
            component={<AddNew cb={getPackageList} {...ADD_PACKAGE_FORM} />}
        />
        <MDBRow>
            {packageList.map((packages: any) => <MDBCol key={packages._id} lg="4">
                <BasicCard {...packages} {...ADD_PACKAGE_FORM} cb={getPackageList} isAdmin={props.userDetails.userType} />
            </MDBCol>)}
        </MDBRow>
    </>
}