import { Button } from '@material-ui/core';
import React, { useEffect, useState } from 'react'

const Image = require('../../assets/icons/placeholder.png')

export default function UploadImageOnly(props: any) {
    const [file, setFile] = useState([]);
    const [isUploadedFile, setIsUploadedFile] = useState(false);

    const onFileChange = (files: any) => {
        setIsUploadedFile(true)
        props.setImageFile(files[0])
        setFile(files);
    };

    useEffect(() => {
        setFile(props.imageFile || [])
    }, [props.imageFile])

    const getMediaUrl = () => {
        if (props.isEdit && !isUploadedFile) {
            return props.file
        }
        if (isUploadedFile && file[0]) {
            return URL.createObjectURL(file[0])
        }
        return String(Image.default)
    }

    return <>
        <div className="text-center">
            <Button
                size="small"
                variant="outlined"
                component="label"
            >
                {props.video ? <video className="w-100 border-0" controls>
                    <source src={getMediaUrl() as any} />
                </video>
                    : <img alt="upload_image" height="200px" src={getMediaUrl() as any} />}
                <input
                    type="file"
                    style={{ display: "none" }}
                    onChange={(e) => onFileChange(e.target.files)}
                />
            </Button>
        </div>
    </>
}