import { List } from '@material-ui/core'
import React, { useEffect, useState } from 'react'
import { updateNotofication } from '../../Actions/ActionTypes';
import { call } from '../../services/Api';
import Calendar from '../Calendar/Calendar';
import CustomListItem from '../Card/CustomListItem'
import Loading from '../RainyDay/Loading';
import NotFound from '../RainyDay/NotFound';

export default function BookingSection(props: any) {
    const [showCalendar, setShowCalendar] = useState(false);
    const [bookingList, setBookingList] = useState([]);
    const [isApiCallInProgress, setIsApiCallInProgress] = useState(false);

    useEffect(() => {
        getBookings();
    }, [])

    const getBookings = () => {
        setIsApiCallInProgress(true)
        call("appointment", 'GET').then((response: any) => {
            setIsApiCallInProgress(false)
            if (response.error || response.message) {
                updateNotofication({ type: "error", message: response.error || response.message });
            } else {
                setBookingList(response || [])
            }
        })
    }


    if (isApiCallInProgress) {
        return <Loading name="Appointments" />
    }

    if (bookingList.length === 0) {
        return <NotFound listType="Appointments" />
    }

    return <>
        <h3 className="d-flex justify-content-between align-items-center">
            <span>Past Appointments</span>
            <i onClick={() => { setShowCalendar(!showCalendar) }} className="fas fa-calendar-alt text-primary pointer"></i>
        </h3>
        {showCalendar && <Calendar slotDetails={props.slotDetails} />}
        <List>
            {bookingList.filter((booking: any) => new Date() > new Date(booking.appointmentTime)).map((booking: any) => <CustomListItem key={booking._id} past={true} {...booking} />)}
        </List >
        <h3 className="d-flex justify-content-between align-items-center">
            <span>Upcoming Appointments</span>
        </h3>
        <List>
            {bookingList.filter((booking: any) => new Date() < new Date(booking.appointmentTime)).map((booking: any) => <CustomListItem key={booking._id} {...booking} cb={getBookings} />)}
        </List >
    </>
}