import React from 'react';
import { MDBRow, MDBCol } from 'mdbreact';
import { Typography, Hidden } from '@material-ui/core';
import { CSS } from '../assets/style';
import CheckIcon from '@material-ui/icons/Check';

export default function PageBanner(props: any) {
    return <div style={{ ...CSS.alignCenter }}
        className={`parallax ${props.image} text-center`}>
        <img className="position-absolute parallax-img w-100 h-100" alt="img" src={props.url} />
        <MDBRow className="m-0 w-50" style={{ zIndex: 9 }}>
            <MDBCol lg={props.otherDetails === undefined ? "12" : "6"} xl={props.otherDetails === undefined ? "12" : "6"} style={CSS.alignCenter}>
                <Typography className="bannerText mb-3" variant="h4">{props.title}</Typography>
                {props.component}
            </MDBCol>
            <Hidden smDown>
                <MDBCol lg="6" xl="6" className="p-3 shadow-lg rounded">
                    <table cellSpacing={3} cellPadding={3}>
                        <tbody className="text-left">
                            {props.otherDetails.map((element: any, index: number) => <tr key={index}>
                                <td><CheckIcon className="mr-3" /></td>
                                <td><Typography variant="h4">{element}</Typography></td>
                            </tr>)}
                        </tbody>
                    </table>
                </MDBCol>
            </Hidden>
        </MDBRow>
    </div >
}