import React from "react";
import { checkIfVideo } from "../../services/util";

export default function MediaCard(props: any) {
    return (
        <figure className="text-center shadow m-0">
            {!checkIfVideo(props) ? <img
                src={props.imageUrl || props.src}
                className="figure-img img-fluid z-depth-1 w-100 m-0"
                alt="" style={{ height: '250px' }}
            /> :
                <video className="w-100 border-0" controls>
                    <source src={props.src} />
                </video>
            }
        </figure>
    );
}