import React from 'react';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import CallIcon from '@material-ui/icons/Call';
import MailIcon from '@material-ui/icons/Mail';
import ScheduleIcon from '@material-ui/icons/Schedule';
import { MDBCol, MDBRow } from 'mdbreact';
import { List, ListItem, ListItemAvatar, ListItemText } from '@material-ui/core';
import { connect } from 'react-redux';
import { CSS } from '../../assets/style';
import { get } from 'lodash';
import CustomIcon from '../CustomIcon';

function HeaderInfoCard(props: any) {
    const staticCardInfo = [{
        icon: "010-doctor",
        title: "Expert Therapists",
        description: "Highly trained team with a very good experience in all sort of Physiotherapies",
        bgColor: "#9e0036",
        color: "#fff"
    }, {
        icon: "022-hospital bed",
        title: "Emergence Service",
        description: "Dont hesitate to give us a call anytime you are in need",
        bgColor: "#ffdc78",
        color: "#222"
    }, {
        icon: "028-massage",
        title: "Free Consultation",
        description: "Consult us with your query. Your wellness is our happiness",
        bgColor: "cornflowerblue",
        color: "#222"
    }]

    const infoElements = [{
        onclick: function () { window.open('https://www.google.com/maps/@18.559885,73.779381,17z?hl=en', '_blank') },
        icon: <LocationOnIcon color="secondary" fontSize="large" />,
        primaryText: "Visit Us",
        secondaryText: get(props, "contactDetails.address", 'Address Not Found').substr(0,40)
    },
    {
        onclick: function () { window.location = `tel:${get(props, "contactDetails.phoneNumbers[0]", null)}` as any },
        icon: <CallIcon color="secondary" fontSize="large" />,
        primaryText: "Call Us",
        secondaryText: get(props, "contactDetails.phoneNumbers[0]", 'Phone Not Found')
    },
    {
        onclick: function () { window.location.href = `mailto: ${get(props, "contactDetails.emailId", null)}` as any },
        icon: <MailIcon color="secondary" fontSize="large" />,
        primaryText: "Message Us",
        secondaryText: get(props, "contactDetails.emailId", 'Email Not Found')
    },
    {
        onclick: function () { },
        icon: <ScheduleIcon color="secondary" fontSize="large" />,
        primaryText: "Operating Hours",
        secondaryText: "Mon-Fri 9AM-6PM"
    }]

    if (props.static) {
        return <MDBRow className="m-0">
            {staticCardInfo.map(element => <MDBCol style={{ background: element.bgColor }} key={element.title} lg="4">
                <List style={{ ...CSS.alignCenter, minHeight: '200px' }} className="mt-3">
                    <ListItem className="shadow">
                        <ListItemAvatar className="mr-5 w-25">
                            <CustomIcon size="100px" icon={element.icon} />
                        </ListItemAvatar>
                        <ListItemText className="text-left">
                            <h2 style={{ color: element.color }}>{element.title}</h2>
                            <p style={{ color: element.color }}>{element.description}</p>
                        </ListItemText>
                    </ListItem>
                </List>
            </MDBCol>)}
        </MDBRow>
    }

    return <MDBRow className="m-0">
        {infoElements.map(element => <MDBCol onClick={element.onclick} className="pointer" key={element.primaryText} lg="3">
            <List className="bg-white shadow p-1 mt-3">
                <ListItem>
                    <ListItemAvatar>
                        {element.icon}
                    </ListItemAvatar>
                    <ListItemText primary={element.primaryText} secondary={element.secondaryText} />
                </ListItem>
            </List>
        </MDBCol>)}
    </MDBRow>
}

const mapStateToProps = (state: any) => {
    return {
        contactDetails: state.UserReducer.contactDetails
    }
}

export default connect(mapStateToProps, {})(HeaderInfoCard);