import { Button } from '@material-ui/core'
import React from 'react'
import { CSS } from '../assets/style'

export default function CustomBtn(props: any) {
    let btnParentClass = "customBtn";
    if (props.disabled) {
        btnParentClass = "customBtnDisabled"
    }
    if (props.btnVariant) {
        btnParentClass = "customBtnText"
    }
    return <span className={btnParentClass}>
        <Button
            fullWidth={props.fullWidth}
            disabled={props.disabled}
            style={props.btnVariant ? {} : { ...CSS.btn }}
            className="border-0"
            variant={props.btnVariant || "outlined"}
            size={props.size || 'small'}
            onClick={props.handleButtonClick}>
            {props.btnLabel}
        </Button>
    </span>
}