import React from 'react'

import { default as GalleryImg } from '../../assets/icons/picture.svg';
import { default as Video } from '../../assets/icons/monitor-screen.svg';
import { default as Contact } from '../../assets/icons/contact.svg';
import { default as Time } from '../../assets/icons/time.svg';
import { default as Service } from '../../assets/icons/package.svg';
import { default as Appointment } from '../../assets/icons/agenda.svg';
import { default as Testimonials } from '../../assets/icons/testimonials.svg';
import { Hidden, Paper } from '@material-ui/core';
import { CSS } from '../../assets/style';

const SECTIONS = [{
    label: "Bookings",
    key: 'appointment',
    src: Appointment
}, {
    label: "Gallery",
    key: 'image',
    src: GalleryImg
}, {
    label: "Videos",
    key: 'video',
    src: Video
}, {
    label: "Services",
    key: 'services',
    src: Service
}, {
    label: "Timing",
    key: 'timing',
    src: Time
}, {
    label: "Testimonials",
    key: 'testimonials',
    src: Testimonials
}, {
    label: "Contact",
    key: 'contact',
    src: Contact
}]

export default function AdminMenu(props: any) {
    return <div className="admin_menu">
        {SECTIONS.map(section => <Paper
            key={section.key}
            style={{ ...CSS.alignCenter }}
            className={`p-2 pointer ${props.visibleSection === section.key ? 'bg-info' : ''}`}
            onClick={() => { props.showSection(section.key) }}>
            <img className="admin_image" alt={section.key} src={section.src} crossOrigin="anonymous" />
            <Hidden smDown>
                <h4 className={`mt-2 ${props.visibleSection === section.key ? 'text-white' : ''}`}>{section.label}</h4>
            </Hidden>
        </Paper>)}
    </div>
}