import React, { useState } from 'react';
import { Tabs, Tab, Typography } from '@material-ui/core';
import InfoTable from './InfoTable';
import Rupee from '../RuppeSign';

export default function MultipleTable(props: any) {
    const [value, setValue] = useState(0);
    const handleChange = (event: React.ChangeEvent<{}>, newValue: number) => {
        setValue(newValue);
    };

    return <div>
        <Tabs
            variant="scrollable"
            className="w-100"
            value={value}
            onChange={handleChange}>
            {props.appointments.map((item: any) => {
                return <Tab key={item._id || item} label={item._id || item} />
            })}
        </Tabs>
        <InfoTable {...props.appointments[value]} />
        <Typography variant="h4" className="mt-2 d-flex justify-content-end align-items-center">
           <Rupee /> {props.appointments.reduce((sum: number, element: any) => sum + element.price, 0)}
        </Typography>
    </div >
}