 import React, { useEffect, useState } from "react";
 import { TextField } from "@material-ui/core";
 import { getValue } from "../../services/Form";
 import { callFormApi } from "../../services/Api";
 import { updateNotofication } from "../../Actions/ActionTypes";
 import Error from "../RainyDay/Error";
 import CustomBtn from "../CustomBtn";
 import UploadImageOnly from "../Dialog/UploadImageOnly";
 import { get } from "lodash";
 
 export default function AddNew(props: any) {
     const [formData, setFormData] = useState({});
     const [isApiCallInProgress, setIsApiCallInProgress] = useState(false);
     const [errorMessage, serErrorMessage] = useState('');
     const [imageFile, setImageFile] = useState({} as any)
     let isEdit: boolean = props.data ? true : false;
 
     useEffect(() => {
         setFormData({ ...formData, ...props.data });
     }, [])
 
     const isInvalidForm = () => {
         let reverseFieldForValidation: Array<any> = [...props.inputFields]
         let isAllInValid: boolean = false;
         reverseFieldForValidation.reverse().forEach((element: any) => {
             if (getValue(element.key, formData) === undefined || getValue(element.key, formData).length === 0) {
                 serErrorMessage("Invalid " + element.label);
                 isAllInValid = true;
             }
         });
         return isAllInValid
     }
 
     const resetForm = () => {
         let newFormData: any = { ...formData };
         Object.keys(formData).forEach((key) => {
             delete newFormData[key]
         })
         setFormData(newFormData);
         setImageFile([]);
         props.cb()
     }
 
     const add = () => {
         if (!imageFile.name && !isEdit) {
             serErrorMessage('Service Image is Required');
             return
         }
         if (isInvalidForm()) {
             return
         }
         serErrorMessage('');
         setIsApiCallInProgress(true);
 
         let formDataRequest = new FormData();
         formDataRequest.append(
             "files",
             imageFile
         );
 
         let requestParamKeys = props.inputFields.map((element: any) => element.key)
         requestParamKeys.forEach((key: string) => formDataRequest.append(key, getValue(key, formData)))
 
         callFormApi(`service`, isEdit ? 'PUT' : 'POST', formDataRequest).then((response: any) => {
             setIsApiCallInProgress(false);
             if (response.error || response.message) {
                 serErrorMessage(response.error || response.message);
                 updateNotofication({ type: "error", message: response.error || response.message });
                 return
             } if (response.code === 11000) {
                 const errorMsg = `Service with Id ${get(response, 'keyValue._id', null)} already Exist `
                 serErrorMessage(errorMsg);
                 updateNotofication({ type: "error", message: errorMsg });
                 return
             } else {
                 updateNotofication({ type: "success", message: isEdit ? "Updated Suceesfully" : "Added Successful" });
                 resetForm();
             }
         })
     }
 
     const handleChange = (stateName: string, value: string) => {
         setFormData({ ...formData, [stateName]: value });
     }
 
     return (
         <form>
             <UploadImageOnly {...props} isEdit={isEdit} file={props.data ? props.data.imageUrl || props.data.src : imageFile} setImageFile={setImageFile} />
             <br />
             <div className="row m-0">
                 {props.inputFields.map((inputField: any) => {
                     return <div key={inputField.label} className={inputField.fullWidth ? "col-md-12" : "col-md-6"}>
                         <TextField
                             size="small"
                             type={inputField.type}
                             multiline={inputField.fullWidth}
                             fullWidth={inputField.fullWidth}
                             rows={3}
                             rowsMax={4}
                             className={"col-md-12"}
                             variant="outlined"
                             margin="normal"
                             label={inputField.label}
                             value={getValue(inputField.key, formData)}
                             onChange={(e) => { handleChange(inputField.key, e.target.value) }}
                             disabled={inputField.disabled}
                         />
                     </div>
                 })}
             </div>
             <div className="text-center m-3">
                 <CustomBtn handleButtonClick={add} btnLabel={isApiCallInProgress ? "Saving..." : isEdit ? "Update" : "Add"} />
             </div>
             <Error errorMessage={errorMessage} />
         </form>
     );
 };