import React from 'react'
import { connect } from 'react-redux'
import { CSS } from '../../assets/style'
import { LoginNew } from '../Login/LoginNew'

function LandingPageRegisterCard(props: any) {
    if (props.userDetails._id !== undefined) {
        return null
    }

    return <div style={{ ...CSS.alignCenter }} className="text-center mt-5 parallax parallax_sigup">
        <h2 className="text-light w-75 m-auto">
            Register today for faster and hassle free bookings
        <br />
            <br />
            <LoginNew button />
        </h2>
    </div>
}

const mapStateToProps = (state: any) => {
    return {
        userDetails: state.UserReducer.userDetails
    }
}

export default connect(mapStateToProps, {})(LandingPageRegisterCard);