import React, { useEffect, useState } from "react";
import { MDBCol, MDBContainer, MDBRow, MDBFooter, MDBIcon } from "mdbreact";
import { COLORS } from "../../assets/style";
import { fetchAPI } from "../../services/Api";
import { MenuItems } from "../../services/Constants";
import { Link } from "react-router-dom";
import { get } from "lodash";
import { connect } from "react-redux";

const Footer = (props: any) => {
    const [slots, setSlots] = useState([]);

    useEffect(() => {
        getOperatingHours();
    }, []);

    const getOperatingHours = () => {
        fetchAPI("operatingSlots").then((res: any) => {
            setSlots(res || [])
        })
    }

    return (
        <MDBFooter className="bg-dark font-small pt-5 footerBg" >
            <MDBContainer className="text-center text-md-left">
                <MDBRow className="text-center">
                    <MDBCol md="3">
                        <div className="ml-1">
                            <h4 className="mb-0 text-light">Nurture Therapies</h4>
                            <small style={{ color: COLORS.app_orange }}>Company Tagline</small>
                        </div>
                        <hr className="hr-light my-4" />
                    </MDBCol>
                    <MDBCol md="3">
                        <h5 className="title text-light">Links</h5>
                        {MenuItems.map((item, index) => {
                            return <p key={index}><Link to={item.value}>{item.label}</Link></p>
                        })}
                    </MDBCol>
                    <MDBCol md="3">
                        <h5 className="title text-light">Contact Us</h5>
                        <ul className="list-unstyled ml-4">
                            <li><MDBIcon icon="map-marker-alt" className="pr-2 text-light" />{get(props.contactDetails, 'address', null)}</li>
                            <li><MDBIcon icon="phone" className="pr-2 text-light" />{get(props.contactDetails, 'phoneNumbers[0]', null)}</li>
                            <li><MDBIcon icon="envelope" className="pr-2 text-light" />{get(props.contactDetails, 'emailId[0]', null)}
                            </li>
                        </ul>
                        <hr className="hr-light my-4" />
                        <ul className="list-inline text-center list-unstyled">
                            <li className="list-inline-item">
                                <a rel="noreferrer" target="_blank" href={get(props.contactDetails, 'social.twitter', null)} className="p-2 fa-lg w-ic">
                                    <MDBIcon fab icon="twitter" />
                                </a>
                            </li>
                            <li className="list-inline-item">
                                <a rel="noreferrer" target="_blank" href={get(props.contactDetails, 'social.fb', null)} className="p-2 fa-lg w-ic">
                                    <MDBIcon fab icon="facebook" />
                                </a>
                            </li>
                            <li className="list-inline-item">
                                <a rel="noreferrer" target="_blank" href={get(props.contactDetails, 'social.insta', null)} className="p-2 fa-lg w-ic">
                                    <MDBIcon fab icon="instagram" />
                                </a>
                            </li>
                        </ul>
                        <hr className="hr-light my-4" />
                    </MDBCol>
                    <MDBCol md="3">
                        <h5 className="title text-light">Opening Hours</h5>
                        <ul className="list-unstyled text-left">
                            {slots.map((day: any) => <li key={day.id} className="p-1 border-bottom">{day.label}
                                <span className={`${day.isClosed ? 'text-danger' : ''} float-right`} > {day.isClosed ? 'Closed' : `${day.startTime} - ${day.endTime}`}</span>
                            </li>)}
                        </ul>
                    </MDBCol>
                </MDBRow>
            </MDBContainer>
            <div className="footer-copyright text-center py-3">
                <MDBContainer fluid>
                    &copy; {new Date().getFullYear()} Copyright: <a href="https://www.theworkboxsolutions.com"> theworkboxsolutions.com </a>
                </MDBContainer>
            </div>
        </MDBFooter>
    );
}

const mapStateToProps = (state: any) => {
    return {
        contactDetails: state.UserReducer.contactDetails
    }
}

export default connect(mapStateToProps, {})(Footer);