import { MDBCol, MDBMask, MDBRow, MDBView } from 'mdbreact'
import React from 'react'
import { CSS } from '../../assets/style'
import CheckIcon from '@material-ui/icons/Check';
import Booking from '../../pages/Booking';

export default function LandingPageCard() {
    return <div style={{ ...CSS.alignCenter }} className="text-center mt-5 parallax parallax_physio">
        <MDBRow className="m-0">
            <MDBCol lg="5">
                <MDBView className="shadow">
                    <img
                        width="100%"
                        alt="img"
                        src="https://image.freepik.com/free-vector/rehabilitation-physiotherapy-isometric-icons-set_1284-20238.jpg" />
                    <MDBMask overlay="black-strong" />
                </MDBView>

            </MDBCol>
            <MDBCol style={{ ...CSS.alignCenter, ...{ 'alignItems': 'flex-start' } }} className="text-left" lg="7">
                <h4 className="text-warning mt-3">SOLUTIONS TO YOUR PAIN</h4>
                <h2 className="text-light mt-3 mb-3">
                    No matter whether your condition was caused by a sport, work
                    accident or otherwise, we welcome the chance to serve you.
            </h2>
                <table className="mb-5 mt-3">
                    <tbody>
                        <tr className="text-light">
                            <td><CheckIcon color="error" className="mr-3" /></td>
                            <td>24 -Hours Emergency Services</td>
                        </tr>
                        <tr className="text-light">
                            <td><CheckIcon color="error" className="mr-3" /></td>
                            <td>Professional and Certified Therapists</td>
                        </tr>
                        <tr className="text-light">
                            <td><CheckIcon color="error" className="mr-3" /></td>
                            <td>Get Free Consultation Anytime</td>
                        </tr>
                    </tbody>
                </table>
                <Booking />
            </MDBCol>
        </MDBRow>
    </div>
}